import CONFIG from '../../globals/config';

const createRestaurantDetailTemplate = (restaurant) => {
        const imageUrl = `${CONFIG.BASE_IMAGE_URL}${restaurant.pictureId}`;
        return `
    <h2 class="restaurant__name">${restaurant.name}</h2>
    <img class="restaurant__poster" src="${imageUrl}" alt="${restaurant.name}" />
    <div class="restaurant__info">
        <h3>Information</h3>
        <h4>Category</h4>
        <p>${restaurant.categories.map(category => category.name).join(', ')}</p>
        <h4>Description</h4>
        <p>${restaurant.description}</p>
        <h4>City</h4>
        <p>${restaurant.city}</p>
        <h4>Address</h4>
        <p>${restaurant.address}</p>
        <h4>Menus</h4>
        <h5>Foods:</h5>
        <p> ${restaurant.menus.foods.map(food => food.name).join(', ')}</p>
        <h5>Drinks:</h5>
        <p>${restaurant.menus.drinks.map(drink => drink.name).join(', ')}</p>
        <h4>Customer Reviews</h4>
        ${restaurant.customerReviews.map(review => `
            <div class="restaurant__review">
                <p>"${review.review}"</p>
                <p>by ${review.name}</p>
                <p><small> On ${review.date}</small></p>
            </div>
            `).join('')}
    </div>
    `;
};

const createRestaurantItemTemplate = (restaurant) => `
  <div class="restaurant-item">
    <div class="restaurant-item__header">
      <img class="restaurant-item__header__poster" alt="${restaurant.name}"
           src="${restaurant.pictureId ? CONFIG.BASE_IMAGE_URL + restaurant.pictureId : 'https://picsum.photos/id/666/800/450?grayscale'}">
      <div class="restaurant-item__header__rating">
        <p><span class="restaurant-item__header__rating__score">${restaurant.rating}</span></p>
      </div>
    </div>
    <div class="restaurant-item__content">
      <h3><a href="/#/detail/${restaurant.id}">${restaurant.name}</a></h3>
      <p>${restaurant.description}</p>
    </div>
  </div>
`;

const createLikeButtonTemplate = () => `
  <button aria-label="like this movie" id="likeButton" class="like">
     <i class="fa fa-heart-o" aria-hidden="true"></i>
  </button>
`;
 
const createLikedButtonTemplate = () => `
  <button aria-label="unlike this movie" id="likeButton" class="like">
    <i class="fa fa-heart" aria-hidden="true"></i>
  </button>
`;

export { 
  createRestaurantItemTemplate, 
  createRestaurantDetailTemplate,
  createLikeButtonTemplate,
  createLikedButtonTemplate
 };